import { Component } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AppService } from '../../services/app.service';
import { PersonalDetailObject } from "../../interfaces/personaldetail.interface";
import { NgxPaginationModule } from 'ngx-pagination';


@Component({
    selector: 'app-root',
    templateUrl: './home.component.html',
    styleUrls: ['../app.component.css']
})
export class HomeComponent {
    private modelData: PersonalDetailObject = new PersonalDetailObject();
    upcomingData: any;
    p: number = 1;
    collection = [];
    message1: boolean = true;
    constructor(private appservice: AppService, private router: Router, private route: ActivatedRoute) {
        this.modelData = this.appservice.getPersonalDetailObject();

        this.getData()
    }
    Dashboard(){
        this.router.navigate(['../dashboard'], { relativeTo: this.route });
    }
    pagecreatedata() {
        this.router.navigate(['../create'], { relativeTo: this.route });
    }
    getData() {
        this.appservice.getPersonalData()
            .subscribe(data => {
                console.log("personal data", data);
                if(data){
                    this.message1 = false;
                    this.collection = data;
                }else{
                    this.message1 = true;
                        }
              
                // this.collection = this.upcomingData;  
            })

    }
}
