import { Component } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { PersonalDetailObject } from "../../interfaces/personaldetail.interface";
import { AppService } from '../../services/app.service';
// import { FileUtil } from './file.util';
// import { Constants } from './test.constants';
declare var Ladda: any;

@Component({
    selector: 'createdata',
    templateUrl: './createdata.component.html',
    styleUrls: ['../app.component.css']
})
export class CreateDataComponent {
    
    private modelData: PersonalDetailObject = new PersonalDetailObject();
    modelalert: string;
    files: any;
    filesTarget: any;
    success: boolean;
    upload_csv_flag: boolean;
    isDisabledName: boolean = true
    isDisabledbutton1: boolean = true;
    isDisabledbutton2: boolean = true;
    isDisabledupload: boolean = true;
    isDisabledbutton3: boolean = true;
    checkbox: boolean;
    checkboxUpload: boolean;
    public laddaLoading: boolean = false;
    message: boolean = false;

    constructor(private appservice: AppService, private router: Router, private route: ActivatedRoute) {
        this.modelData = this.appservice.getPersonalDetailObject();
    }
    Dashboard() {
        this.router.navigate(['../dashboard'], { relativeTo: this.route });
    }
    home() {
        this.router.navigate(['../home'], { relativeTo: this.route });
    }
    addPersonalDetails() {
        console.log("model", this.modelData);
        // this.modelalert = "done";
        this.appservice.addDetails(this.modelData)
            .subscribe(response => {
                console.log("response", response);
                if (response) {
                    // console.log("hi", this.response)
                    this.modelData.name = '';
                    this.modelData.file_name = ''
                    this.modelData.id = ''
                    this.router.navigate(['../home'], { relativeTo: this.route });
                }
            })
    }
    fileChange($event) {
        this.files = $event.srcElement.files;
        this.filesTarget = $event.target;

        if (this.files[0] == '' || this.files[0] == undefined) {
            this.success = false;
            this.modelData.file_name = '';
        }
        else {
            this.modelData.file_name = this.files[0].name;
            this.upload_csv_flag = true;
        }
    }
    fileChangeListener(action): void {
        // this.isLoading1 = true;
        this.laddaLoading = true;
        this.isDisabledbutton3 = false
        this.appservice.uploadFile(this.files[0]).subscribe(res => {
            if (res) {
            this.laddaLoading = false;
            this.modelData.file_name = '';   
              this.message = true;
                
            }
        });

    };
    checkBoxSelected(e) {
        if (e.target.checked) {
            console.log("tagget", e.target.checked)
            this.checkboxUpload = false
            this.isDisabledName = false
            this.isDisabledbutton1 = false;
            this.isDisabledbutton2 = false;
            this.isDisabledupload = true;
            this.isDisabledbutton3 = true
        }
        else {
            this.isDisabledName = true
            this.isDisabledbutton1 = true;
            this.isDisabledbutton2 = true;
            this.isDisabledupload = false;
            this.isDisabledbutton3 = false
        }
    }
    checkBoxSelectedUpload(e) {
        if (e.target.checked) {
            console.log("tagget", e.target.checked)
            this.checkbox = false;
            this.isDisabledupload = false;
            this.isDisabledbutton3 = false
            this.isDisabledName = true
            this.isDisabledbutton1 = true;
            this.isDisabledbutton2 = true;
        }
        else {
            this.isDisabledupload = true;
            this.isDisabledbutton3 = true
            this.isDisabledName = false
            this.isDisabledbutton1 = false;
            this.isDisabledbutton2 = false;
        }
    }
    close() {
        this.router.navigate(['../home'], { relativeTo: this.route });
    }
}
