import { Injectable, EventEmitter } from "@angular/core";
import { Http, Headers, Response } from "@angular/http";
import 'rxjs/Rx';
import { Observable } from "rxjs";
import { map, filter, switchMap } from 'rxjs/operators';
import { PersonalDetailObject } from "../interfaces/personaldetail.interface";
// import { HttpClient } from 'selenium-webdriver/http';

@Injectable()
export class AppService {
    public modelPersonalDetailObject = new PersonalDetailObject('');
    PersonalDetailObject = new PersonalDetailObject();


    public apiURL = 'http://localhost:5000/'

    constructor(private http: Http) { }

    // interface methords

    setPersonalDetailObject(personalDetailObject: PersonalDetailObject) {
        this.PersonalDetailObject = personalDetailObject;
    }
    getPersonalDetailObject() {
        return this.PersonalDetailObject;
    }
    addDetails(addData: PersonalDetailObject){
        const body = JSON.stringify(addData);
        console.log("service",body)
        const headers = new Headers({ 'Content-Type': 'application/json' });
        return this.http.post(this.apiURL + 'bucketlists/', body, { headers: headers }).map((response: Response) => response.json());
    }
  
    getPersonalData(){
        return this.http.get(this.apiURL + 'bucketlists/').map((response: Response) => response.json())
    }
 
    uploadFile(file){
        let formData = new FormData();
        formData.append('file', file);
        console.log("form Data",formData)
        return this.http.post(this.apiURL + 'bucketlists/upload/', formData)
        .map((response: Response) => response.json());

    }
  

  
}
